.footer{
  color: @customblue;
  & .container{
    padding: 40px 74px;

    display: flex;
    flex-direction: column;

    justify-content: center;

    @media(min-width:@tablet-width){
      flex-direction: row;
      align-items: center;
      justify-content: space-between;

      padding: 35px 20px;
    }

    @media(min-width:@desktop-width){
      padding: 75px 130px;
    }
  }

