  &__social{
    padding: 35px 0;

    display: flex;
    align-items: center;
    justify-content:space-between;

    @media(min-width:@tablet-width){
      padding: 0;
    }
  }

  &__copyright{
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.social{
  &__link{
    // outline: 3px solid @customblue;
    border-radius: 50%;

    min-width: 48px;
    min-height: 48px;

    border: 3px solid #28364510;

    display: flex;
    align-items: center;
    justify-content: center;

    fill: @custompink;

    &:hover,
    &:focus{
      border: 3px solid #283645;
    }

    &:active{
      opacity: 0.3;
    }

    @media(min-width:@tablet-width){
      margin: 0 7px;
    }

    @media(min-width:@desktop-width){
      margin: 0 3px;
    }
  }
}

.copyright{
  &__text{
    font-size: 14px;
    line-height: 18px;

    text-decoration: none;
    display: flex;
    align-items: center;

    &:hover,
    &:focus{
      & .copyright__logo{
        fill: #000000;
      }
    }

    &:active{
      & .copyright__logo{
        fill: #00000030;
      }
    }

    & span{
      margin-bottom: 3px;
      display: block;

      padding-right: 15px;
    }

    @media(min-width:@desktop-width){
      font-size: 18px;
      line-height: 18px;
    }
  }

  &__logo{
    fill: @custompink;
  }
}
