.header {
  background-color: #00000030;
  height: 66px;
  position: absolute;
  width: 100%;
  z-index: 1;

  & .container{
    display: flex;
    width: 100%;
    height: 100%;
    @media(min-width:@desktop-width){
      justify-content: space-between;
    }
  }


  @media(min-width:@tablet-width){
    height: 110px;
  }

  &__inner{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;



    @media(min-width:@desktop-width){
      padding-bottom: 15px;
    }

  }

