.nav{
  position: absolute;
  top: 66px;
  left: 0;

  width: 100%;

  border-top: 1px solid #000000;
  z-index: 1;

  @media(min-width:@tablet-width){
    top: 110px;
  }


  @media(min-width:@desktop-width){
    display: flex;
    align-items: center;
    position: static;
    width: auto;
    justify-content: stretch;

    border: 0;

  }

  &--closed{
    display: none;
  }

  &--opened{
    display: block;


  }

  @media(min-width:@desktop-width){
    display: flex;
  }

  &__item{
    text-align: center;
    justify-content: center;
    // align-items: center;
    display: block;
    text-decoration: none;
    color: #ffffff;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 18px;
    line-height: 32px;
    background-color: @customblue;
    // height: 63px;

    padding: 15px 0;

    box-shadow: 0px 1px 0px #000000, inset 0px 1px 0px rgba(255, 255, 255, 0.15);
    border-bottom: 1px solid #000000;

    position: relative;

    &:hover,
    &:focus{
      color: @custompink;
    }

    &:active{
      color: #D2285630;
    }

    @media(min-width:@desktop-width){
      background-color: transparent;
      text-transform: none;
      font-weight: 400;
      line-height: 18px;
      border: 0;
      box-shadow: none;
      padding: 0 39px;
    }


    &:last-child{
      padding-right: 0;
    }

    &--current{
      &::before{
        position: absolute;
        content: "";

        width: 100%;
        height: 2px;

        bottom: -20px;
        left: 0;

        background-color: #ffffff;
      }
    }
  }

  &__toggle{
    width: 50px;
    height: 24px;
    border: 0;
    background-color: transparent;
    position: relative;

    &--closed{
    &::before {
      position: absolute;
      content: "";
      width: 50px;
      height: 4px;
      border-radius: 5px;
      background-color: #ffffff;
      box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
      z-index: 1000;
      top: 0;
      left: 0;
    }

      &:focus::before,
      &:hover::before {
        background-color: @custompink;
        box-shadow: 0 10px 0 0 #D22856, 0 20px 0 0 @custompink;
      }
    }

    &--opened{
      &::before,
      &::after {
        content: "";
        position: absolute;
        width: 23px;
        height: 3px;
        border-radius: 5px;
        background-color: #ffffff;
        top: 10px;
        left: 15px;
        }

        &::before {
          transform: rotate(45deg);
        }

        &::after {
          transform: rotate(-45deg);
        }

        &:focus::before,
        &:focus::after,
        &:hover::after,
        &:hover::before {
          background-color: @custompink;
        }
    }





    @media(min-width:@desktop-width){
      display: none;
    }
  }
}
